@keyframes example {
    from {
        transform: translateX(-100%);
    }

    to {
        transform: translateX(0%);
    }
}

.sidebar {
    margin: 0;
    width: 155px;
    height: 100%;
    margin-bottom: 0;
    top: 54px;
    bottom: 0;
    margin-top: 54px;
    min-width: 155px;
    position: sticky;
    overflow-y: auto;
    z-index: 100;
    background-color: var(--background);
    animation-name: example;
    animation-duration: 0.3s;
    /* height: 100%; */
    transition: all 0.3s;
    min-height: 100%;
    /* border: green 2px solid; */
}

.sidebar>ul {
    list-style: none;
    list-style-type: none;
    padding: 4px;
    margin: 0;
}

.sidebar>ul>li {
    padding: 4px;
    padding-inline: 8px;
    margin: 2px;
    margin-left: 2px;
    border-radius: 10px;
    font-size: 12px;
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: default;
}

.sidebar>ul>li>span {
    margin-right: 10px;
    font-size: 24px;
    color: #ffffff;
}

.sidebar>ul>li.active {
    background-color: #272727;
}

.sidebar>ul>li:hover {
    background-color: #272727;
}

.disclaimer {
    color: grey;
    font-size: small;
    margin: 5px;
}


@media only screen and (max-width: 768px) {
    .sidebar {
        position: fixed;
    }
}