.videoInfo {
    margin: 10px;
    /* background-color: grey; */
    display: flex;
    flex-direction: column;
}

.videoTitle {
    font-weight: 500;
    margin-bottom: 5px;
    width: 100%;
}

.channelInfo {
    display: flex;
    flex-direction: row;
    width: 100%;
    /* background-color: aqua; */
    align-items: center;
    width: max-content;
}

.channelIcon {
    border-radius: 50%;
    height: 35px;
    width: 35px;
    margin: 5px;
    margin-left: 0;
    margin-right: 8px;
}

.channelSubs {
    font-size: x-small;
}

.channelName {
    font-weight: 500;
    font-size: 0.9rem;
    width: max-content;
    white-space: nowrap;
}

.subscribeBtn {
    background-color: whitesmoke;
    color: black;
    padding-inline: 10px;
    height: 30px;
    margin-inline: 5px;
    margin-left: auto;
    border: none;
    border-radius: 20px;
    font-size: small;
    font-weight: 500;

}

.chnlinfo-optns {
    display: flex;
    flex-direction: row;
    align-items: center;
    /* background-color: pink; */
    width: 100%;
}

.options {
    display: flex;
    flex-direction: row;
}

.options>button {
    padding-inline: 12px;
    margin: 3px;
    background-color: var(--secondary);
    border-radius: 30px;
    border: none;
    height: 30px;
    font-size: small;
    color: white;
}

.descriptionBox {
    background-color: var(--secondary);
    border-radius: 12px;
    font-size: small;
    padding: 12px;
    margin-block: 8px;
}

.descriptionBox:hover {
    background-color: #3b3b3be5;
}

.descriptionBox>pre {
    overflow: auto;
}

#mainplayercontainer {
    width: 97%;
    min-height: 100vh;
    height: auto;
    /* // backgroundColor: 'red', */
    display: flex;
    flex-direction: row;
    max-width: 1840px;
    justify-content: center;
    align-content: center;
    /* alignItems: center */
    margin: 0;
    padding: 0;
    padding-top: 5px;
}

#player-container {
    display: flex;
    flex-direction: column;
    /* justifyContent: 'center', */
    /* placeContent: 'center', */
    width: 70%;
}

#videoPlayer {
    min-width: 300px;
    min-height: 240px;
    height: auto;
    /* // width: "100%", */
    /* // maxWidth: "1220px", */
    padding: 10px;
}

#recommendedVids {
    width: 30%;
}

@media only screen and (max-width: 768px) {
    .options {
        display: none;
    }

    #mainplayercontainer {
        width: 100%;
        flex-direction: column;
    }

    #player-container {
        width: 100%;
    }

    #recommendedVids {
        width: 100%;
        max-width: 740px;
    }

    #videoPlayer {
        padding: 0;
    }

    .descriptionBox:hover {
        background-color: var(--secondary);
    }
}