.card {
    /* width: 280px; */
    padding: 0.25rem;
    /* justify-content: space-between; */
}

.cardBody {
    padding: 0px;
    bottom: 0;
    height: 270px;
    /* padding: 2px; */
}

.thumbnail {
    height: 150px;
    /* min-width: 250px; */
    /* width: 19vw; */
    /* max-width: 260px; */
    background-color: grey;
    border-radius: 8px;
    object-fit: cover;
}

.vc-timestamp {
    position: absolute;
    top: 0;
    right: 0;
    margin: 5px;
    background-color: black;
    border-radius: 5px;
    font-size: small;
    padding-inline: 2px;
}

.channelInfo {
    display: flex;
    flex-direction: row;
    margin: 5px;
    margin-left: 0;
    margin-top: 12px;
}

.channel-icon {
    background-color: grey;
    height: 32px;
    width: 32px;
    margin: 3px;
}

.card-title {
    font-weight: 500;
    margin-left: 8px;
    width: auto;
    max-width: 200px;
    height: 75px;
}

.videoTitle {
    max-height: 40px;
    font-size: 0.9rem;
}

.more-info {
    font-size: 12px;
    color: grey;
    padding: 0;
}

.channelName {
    padding: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

@media only screen and (max-width: 640px) {
    .card {
        width: 100%;
        margin: 0;
        padding: 0;
        max-width: 100%;
    }

    .cardBody {
        background-color: aqua;
        height: max-content;
        margin: 0;
        padding: 0;
        width: 99vw;
        max-width: 100vh;
        right: 0;
        left: 0;
    }

    .thumbnail {
        /* width: 100%; */
        /* max-width: 100vw; */
        height: 100%;
        /* max-height: 100%; */
        /* position: relative; */

        /* right: 0;
        left: 0; */
        border-radius: 0;
    }

    .card-title {
        width: 75vw;
        max-width: 85%;
    }

    .channelInfo {
        /* margin: 6px; */
    }

    .channel-icon {
        top: 0;
        margin: 8px;
        margin-top: 0;
        padding-top: 0;
        /* margin-bottom: 8px; */
        margin-right: 2px;
        width: 37px;
        height: 37px;
    }

}